import React from 'react'
import { graphql } from 'gatsby'
import MainLayout from '../layouts/MainLayout'
import LandingView from '../views/LandingView'

function IndexPage({ data }) {
    const topicsCount = data.topics.group.reduce((accumulator, { fieldValue, totalCount }) => ({ ...accumulator, [fieldValue]: totalCount }), {})
    const categories = data.categories.nodes.map((category) => ({ ...category, count: topicsCount[category.frontmatter.category] }))

    return (
        <MainLayout>
            <LandingView categories={categories} totalTopics={data.topics.totalCount} />
        </MainLayout>
    )
}

export const query = graphql`
    query {
        topics: allMarkdownRemark(filter: { fields: { collection: { eq: "topics" } } }) {
            totalCount
            group(field: frontmatter___categories) {
                fieldValue
                totalCount
            }
        }

        categories: allMarkdownRemark(filter: { fields: { collection: { eq: "categories" } } }) {
            nodes {
                id
                html
                frontmatter {
                    category
                    icons
                    title
                    slug
                }
            }
        }
    }
`

export default IndexPage
